export const generateMonthColumnSet = (propsList, module = 'word') => {
  const p = [
    {
      prop: 'InAmount',
      label: '入库金额'
    },
    {
      prop: 'InNumber',
      label: '入库数量'
    },
    {
      prop: 'OutAmount',
      label: '出库金额'
    },
    {
      prop: 'OutNumber',
      label: '出库数量'
    },
    {
      prop: 'InventoryAmount',
      label: '库存金额'
    },
    {
      prop: 'InventoryNumber',
      label: '库存数量'
    }
  ]
  return propsList.map(prop => {
    return {
      prop,
      children: p.map(p => {
        return {
          module,
          prop: `${prop}${p.prop}`,
          label: p.label,
          align: 'center',
          width: module === 'input' ? 100 : '',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      })
    }
  })
}
