<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="month" class="fl">
          <el-date-picker
            v-model="query.month"
            type="month"
            style="width: 178px;"
            clearable
            value-format="yyyy-MM"
            format="yyyy年M月"
            placeholder="选择月份"
          />
        </el-form-item>
        <el-form-item prop="startDate" class="fl">
          <el-date-picker
            v-model="query.startDate"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="开始时间"
            clearable
            :editable="false"
          />
        </el-form-item>
        <el-form-item prop="endDate" class="fl">
          <el-date-picker
            v-model="query.endDate"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="结束时间"
            clearable
            :editable="false"
          />
        </el-form-item>
      </el-form>
      <el-button
        class="filter-item"
        type="success"
        icon="el-icon-search"
        @click="searchInit"
      >搜索</el-button>
      <el-button
        class="filter-item"
        type="warning"
        icon="el-icon-refresh-left"
        @click="resetForm('filterItem')"
      >重置</el-button>
      <!-- <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        :loading="exportLoading"
        @click="handleExport"
      >导出</el-button> -->
    </div>

    <el-row>
      <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-setting"
        @click="setTable"
      >设置</el-button>
      <el-button
        class="filter-item"
        type="info"
        icon="el-icon-setting"
        :loading="dialogLoading"
        @click="openDialog"
      >自定义统计</el-button>
      <!-- <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        :loading="exportLoading"
        @click="handleExport"
      >导出</el-button> -->
      <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        @click="handleExport1"
      >导出</el-button>
    </el-row>

    <!-- 图表折叠面板 -->
    <el-collapse v-model="collapseValue" accordion style="margin: 0 0 20px 0;" @change="$refs.chartCom.reset()">
      <el-collapse-item title="图表展示" name="1">
        <chart-com ref="chartCom" :query="query" style="height: 310px;" />
      </el-collapse-item>
    </el-collapse>

    <!-- 列表 -->
    <table-com
      :ref="refTableName"
      :key="baseTableKey"
      :ref-name="refName"
      class="base-table"
      :table-height-type="collapseValue ? 365 : 615"
      :loading="loading"
      :index="true"
      :selection="false"
      :table-column-sort="true"
      :form-ident="formIdent"
      :table-data="tableData"
      :column-list="columnList"
      :operation-width="120"
      :refresh-key="refreshKey"
      :expand-list="expandList"
      :row-merge-arr="['warehouseArea']"
      @rowClick="handleRowClick"
      @refresh="searchInit"
    >
      <template slot-scope="scope">
        <el-button type="warning" icon="el-icon-edit" @click="handleModify(scope.row)">详情</el-button>
      </template>
    </table-com>

    <!-- 编辑弹窗 -->
