  methods: {
    initQueryTime() {
      this.query.startDate = moment(new Date()).subtract(1, 'months').format('YYYY-MM-DD')
      this.query.endDate = moment(new Date()).format('YYYY-MM-DD')
    },
    init(type) {
      this.loading = true
      const params = type ? { ...this.query } : {
        ...this.query
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.baseTableKey++
            const { formIdent, formHeadSets, pageList } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            // 设置自定义统计弹窗表格的表头
            this.columnListInput = this.initTableHeadMixins(formHeadSets, 'input', 'columnSetsInput')
            const pageData = pageList || []
            if (pageData.length) {
              this.monthMap = { ...pageData[0].pageData }
            }
            this.tableData = []
            pageData.forEach(i => {
              const warehouseAreaParams = i.warehouseArea
              const a = i.warehouseArea.indexOf('_')
              const warehouseArea = i.warehouseArea.substring(0, a)
              const obj = { ...i.monthMap, ...i, warehouseArea, warehouseAreaParams }
              this.tableData.push(obj)
            })
            // this.handleSubmitCustomData()
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
