      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        deleteListApi({
          id: item.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
            this.init()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    /*
    * 用户修改完数据点击确定
    * 1. 提交所有数据给后台，后台生成echarts需要的数据
    * 2. 更新echarts
    */
    onDialogConfirm() {
      this.tableData = this.dialogTableData
      this.getChartData()
      this.dialogVisible = false
    },
    handleSubmitCustomData() {
      const table = this.tableData.map(i => {
        const monthMap = {}
        Object.keys(this.monthMap).forEach(key => {
          monthMap[key] = i[key]
          delete i[key]
        })
        return {
          monthMap,
          ...i
        }
      })
      console.log('提交所有数据给后台，后台生成echarts需要的数据', table)
      return table
    },
    async getChartData() {
      const res = await requestWrapper(wmsInventoryAnalysisRefreshApi({
        dataList: this.handleSubmitCustomData()
      }))
      if (!res.success) return
      // 图1234
      const { inventoryPieChart, agePieChart, monthlyColumnChart, inventoryTurnoverColumnChart } = res.data
      this.updatePieChart('InventoryCom', inventoryPieChart)
      this.updatePieChart('YearChartCom', agePieChart)
      this.updateColumnChart('Ratio', monthlyColumnChart.series)
      this.updateColumnChart('TurnoverRateCom', inventoryTurnoverColumnChart.series)
    },
    updateColumnChart(name, series) {
      const chartCom = this.$refs.chartCom
      const com = chartCom.$refs[name]
      com.options.series = series
      com.chart.setOption(com.options)
    },
    updatePieChart(name, series) {
      const chartCom = this.$refs.chartCom
      const com = chartCom.$refs[name]
      com.options.series[0].data = series
      com.chart.setOption(com.options)
    },
