<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    :visible="value"
    :size="750"
    :destroy-on-close="true"
    direction="btt"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="query-more-scroll-drawer">
      <!-- 头部搜索 -->
      <el-row>
        <table-com
          :ref="refName"
          :loading="loading"
          :index="true"
          :selection="false"
          :table-column-sort="true"
          :table-height-type="620"
          :form-ident="formIdent"
          :table-data="tableData"
          :column-list="columnList"
          :show-operate="false"
          :refresh-key="refreshKey"
          :expand-list="expandList"
          :row-merge-arr="['warehouseArea']"
        />
      </el-row>
    </el-scrollbar>
    <div class="drawer-footer">
      <el-button type="primary" @click="confirmDialog">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-drawer>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'

export default {
  name: 'InventoryCustomEditCom',
  mixins: [paginationPage],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '自定义统计编辑'
    },
    formIdent: {
      type: String,
      default: ''
    },
    tableData: {
      type: Array,
      default: () => []
    },
    columnList: {
      type: Array,
      default: () => []
    },
    expandList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      refName: 'InventoryCustomEditComTable',
      refreshKey: new Date().getTime(),
      form: {},
      loading: false,
      rules: {},
      query: {}
    }
  },
  methods: {
    confirmDialog(formName) {
      this.$emit('dialogConfirm')
    },
    initSelect() {

    },
    beforeClose(done) {
      console.log('执行了')
      this.tableData = new Array(0)
      // this.form.wmsOrderMaterialJoinList = new Array(0)
      this.$emit('input', false)
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  .container{
    width: 860px;
    margin: 0 auto;
    .left{
      width: 640px;
    }
    .right{
      margin-left: 20px;
      width: 180px;
    }
  }
  ::v-deep:focus {
    outline: 0;
  }
  // .scroll-drawer {
    // height: calc(100vh - 150px);
    // overflow: auto;
  // }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }

  .drawer-footer{
    text-align: right;
  }
  </style>

