<template>
  <el-row>
    <el-row :gutter="20" style="height: 100%;">
      <!-- 库存占比图 -->
      <el-col :span="8" style="height: 100%;">
        <inventory ref="InventoryCom" :key="viewKey" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <!-- 账龄占比 -->
      <el-col :span="8" style="height: 100%;">
        <account-rate ref="YearChartCom" :key="viewKey" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <!-- 月度库存 -->
      <el-col :span="8" style="height: 100%;">
        <month-inventory ref="Ratio" :key="viewKey" v-bind="$attrs" style="height: 100%;" />
      </el-col>
    </el-row>
    <el-row :gutter="20" style="height: 100%;">
      <!-- 库存周转率 -->
      <el-col :span="8" style="height: 100%;">
        <inventory-transfer-ratio ref="TurnoverRateCom" :key="viewKey" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <!-- 库存账龄图 -->
      <el-col :span="8" style="height: 100%;">
        <inventory-account
          ref="InventoryAccount"
          :key="viewKey"
          v-bind="$attrs"
          :chart-loading="loading"
          style="height: 100%;"
          :chart-data="amountChart"
          @init="init5and6Charts"
        />
      </el-col>
      <!-- 库存账龄占比 -->
      <el-col :span="8" style="height: 100%;">
        <inventory-age-rate
          ref="InventoryAccountRatio"
          :key="viewKey"
          v-bind="$attrs"
          :chart-loading="loading"
          style="height: 100%;"
          :chart-data="percentageChart"
          @init="init5and6Charts"
        />
      </el-col>
    </el-row>
  </el-row>

</template>

<script>
import Inventory from './Inventory'
import AccountRate from './AccountRate'
import MonthInventory from './MonthInventory'
import InventoryTransferRatio from './InventoryTransferRatio'
import InventoryAccount from './InventoryAccount'
import InventoryAgeRate from './InventoryAgeRate'
import { getChart5and6Api } from '@/apis/modules/analysis/inventory'

export default {
  name: 'ChartsCom',
  components: {
    Inventory,
    AccountRate,
    MonthInventory,
    InventoryTransferRatio,
    InventoryAccount,
    InventoryAgeRate
  },
  data() {
    return {
      viewKey: 1,
      loading: false,
      amountChart: {},
      percentageChart: {}
    }
  },
  methods: {
    init5and6Charts() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getChart5and6Api({
          ...this.$attrs.query
        }).then(res => {
          const { code, data } = res
          if (!code) {
            console.log(data)
            // amountChart 库存账龄图  percentageChart 库存账龄占比
            const { amountChart, percentageChart } = data
            this.amountChart = amountChart
            this.percentageChart = percentageChart
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    reset() {
      this.viewKey++
      this.$refs.YearChartCom.resize()
      this.$refs.InventoryCom.resize()
      this.$refs.Ratio.resize()

      this.$refs.TurnoverRateCom.resize()
      this.$refs.InventoryAccount.resize()
      this.$refs.InventoryAccountRatio.resize()
    },
    handleUploadImg() {

    }
  }
}
</script>

<style lang='scss' scoped>

</style>
